var subArray = [];
const storeKey = 'appState-' + process.env.REACT_APP_APP_IDENTIFIER;

let mainState = {
    key: storeKey,
};
console.log('Store key', storeKey)

let stringMainState = localStorage.getItem(storeKey);
if (stringMainState) {
    try {
        const newState = JSON.parse(stringMainState);
        if (newState.key === storeKey) {
            mainState = newState;
        } else {
            localStorage.removeItem(storeKey);
        }
    }
    catch (err) {
        localStorage.removeItem(storeKey);
    }
}



function subcribe(callback) {
    if (subArray.indexOf(callback) < 0)
        return subArray.push(callback);
}

function unSubcribe(callback) {
    let position = subArray.indexOf(callback);
    if (position >= 0) {
        return subArray.splice(position, 1);
    }
}

async function notice(state) {
    mainState = { ...mainState, ...state };
    localStorage.setItem(storeKey, JSON.stringify(mainState));
    for (let i = 0; i < subArray.length; i++) {
        try {
            await subArray[i](mainState);
        }
        catch (err) {
            console.log('----------NOTICE CALL FUNCTION------', err);
        }
    }
}

function getState() {
    return mainState;
}

export default {
    subcribe,
    unSubcribe,
    notice,
    getState,
}